/*******************************************************************************
 * Themes
 ******************************************************************************/

@use "sass:meta";
@use "variables" as *;

:root {
 // Spatial units (8pt grid)
 --space-mu: 0.25rem; // 4px
 --space-sigma: 0.5rem; // 8px
 --space-tau: 1rem; // 16px
 --space-2tau: 2rem; // 32px

 // Typography - Modular Scale (1.25 Major Third)
 --type-scale-ratio: #{$type-scale-ratio};
 --type-size-caption: #{$type-size-caption};
 --type-size-sm: #{$type-size-sm};
 --type-size-base: #{$type-base-size};
 --type-size-md: #{$type-size-md};
 --type-size-lg: #{$type-size-lg};
 --type-size-xl: #{$type-size-xl};
 --type-size-2xl: #{$type-size-2xl};

 // Font stacks (use inspect to preserve quotes)
 --font-sans: #{meta.inspect($font-sans)};
 --font-serif: #{meta.inspect($font-serif)};
 --font-mono: #{meta.inspect($font-mono)};

 // Content measure (optimal line length)
 --content-measure: #{$content-measure};
 --content-measure-narrow: #{$content-measure-narrow};
 --content-measure-wide: #{$content-measure-wide};

 // Background colors
 --global-bg-color: #{$white-color};
 --global-code-bg-color: #{$code-bg-color-light};
 --global-card-bg-color: #{$white-color};

 // Text colors (systematic blacks: 85%, 60%, 40%)
 --global-text-color: #262626; // 85% black (~15:1 contrast)
 --global-text-color-light: #666; // 60% gray for metadata
 --global-text-color-lighter: #999; // 40% gray for deemphasized
 --global-text-color-dark: #{$black-color};

 // Theme color (Stanford red)
 --global-theme-color: #{$theme-color};
 --global-theme-color-dark: #{$theme-color-dark};
 --global-hover-color: #8C1515; // Cardinal red

 // Structural colors
 --global-divider-color: rgb(38 38 38 / 12%); // hairline from text color

 // Footer
 --global-footer-bg-color: #{$grey-color-dark};
 --global-footer-text-color: #{$grey-color-light};
 --global-footer-link-color: #{$white-color};

 // Legacy (to be phased out)
 --global-distill-app-color: #{$grey-color};

}

html[data-theme='dark'] {
 --global-bg-color: #{$grey-color-dark};
 --global-code-bg-color: #{$code-bg-color-dark};
 --global-text-color: #{$grey-color-light};
 --global-text-color-light: #{$grey-color-light};
 --global-theme-color: #{$cyan-color};
 --global-hover-color: #{$cyan-color};
 --global-footer-bg-color: #{$grey-color-light};
 --global-footer-text-color: #{$grey-color-dark};
 --global-footer-link-color: #{$black-color};
 --global-distill-app-color: #{$grey-color-light};
 --global-divider-color: #424246;
 --global-card-bg-color: #{$grey-900};

}
