/*******************************************************************************
 * About - Profile section styles
 ******************************************************************************/

@use "variables" as *;

.contact-section {
 margin-top: 0;
 padding-top: 2rem;
}

// Profile card
.profile {
 // Don't set width to 100% - prevents floating!
 // Width is set by media query below
 max-width: 100%;

 figure {
 margin-bottom: 0.5rem;
 }

 img[src*="prof_pic"] {
 filter: saturate(0.7);
 }

 .address {
 margin-bottom: var(--space-mu);
 margin-top: var(--space-mu);
 font-family: monospace;

 p {
 display: inline-block;
 margin: 0;
 }
 }

 .social-icons {
 margin-top: 0.5rem;
 text-align: center;

 .contact-icon {
 display: flex;
 justify-content: center;
 gap: var(--space-sigma);

 a {
 color: var(--global-text-color);
 font-size: 1.5rem;

 &:hover {
 color: var(--global-theme-color);
 }
 }

 i {
 padding: 0;
 }
 }
 }
}

.profile.float-right {
 float: right;
 margin-left: 1.125rem;
 margin-bottom: 0;
 margin-top: 0;
 width: 24%; // Set explicit width for floating to work
}

.profile.float-left {
 float: left;
 margin-right: 1.5rem;
 margin-bottom: 1rem;
 width: 24%; // Set explicit width for floating to work
}

@media (width >= 576px) {
 .profile.float-right,
 .profile.float-left {
 width: 20%; // Slightly narrower on larger screens
 }

 .profile {
 .address {
 p {
 display: block;
 }
 }
 }
}
