/*******************************************************************************
 * Components - Reusable UI elements (buttons, badges, cards, etc.)
 ******************************************************************************/

@use "variables" as *;

// Accessibility: Skip to main content link
.skip-link {
 position: absolute;
 top: -40px;
 left: 0;
 background: var(--global-theme-color);
 color: white;
 padding: var(--space-sigma) var(--space-tau);
 text-decoration: none;
 z-index: 100;
 font-weight: 600;

 &:focus {
 top: 0;
 }
}

// Accessibility: Focus visible indicators for keyboard navigation
a:focus-visible,
button:focus-visible,
input:focus-visible,
textarea:focus-visible,
select:focus-visible,
[tabindex]:focus-visible {
 outline: 2px solid var(--global-theme-color);
 outline-offset: 2px;
}

// Button that looks like text
button.more-authors {
 background: none;
 border: none;
 padding: 0;
 font: inherit;
 color: inherit;
 cursor: pointer;
 text-decoration: underline;

 &:hover {
 color: var(--global-hover-color);
 }

 &:focus-visible {
 outline: 2px solid var(--global-theme-color);
 outline-offset: 2px;
 border-radius: 2px;
 }
}

// Card component
.card {
 background-color: var(--global-card-bg-color);

 img {
 width: 100%;
 }

 .card-title {
 color: var(--global-text-color);
 }

 .card-item {
 width: auto;
 margin-bottom: var(--space-sigma);

 .row {
 display: flex;
 align-items: center;
 }
 }
}

// Badge
.my-badge {
 font-weight: 400;
 color: var(--global-text-color-light);
 background-color: transparent;
 border: 1px solid var(--global-divider-color);
 font-size: 0.75rem;
 display: inline-block;
 padding: var(--space-mu) var(--space-sigma);
 white-space: nowrap;
 letter-spacing: 0.02em;
}

// CV section
.cv {
 margin-bottom: 40px;

 .card {
 background-color: var(--global-card-bg-color);
 border: 1px solid var(--global-divider-color);

 .list-group-item {
 background-color: inherit;
 border-color: var(--global-divider-color);

 .badge {
 color: var(--global-card-bg-color) !important;
 background-color: var(--global-theme-color) !important;
 }
 }
 }
}

// Progress bar (reading progress)
progress {
 position: fixed;
 left: 0;
 top: 56px;
 z-index: 10;
 width: 100%;
 height: 1.5px;
 appearance: none;
 border: none;
 background-color: transparent;
}

progress::-webkit-progress-bar {
 background-color: #c5c5c5
}

progress::-webkit-progress-value {
 background-color: var(--global-theme-color);
}

progress::-moz-progress-bar {
 background-color: var(--global-theme-color);
}

.progress-container {
 width: 100%;
 background-color: transparent;
 position: fixed;
 top: 56px;
 left: 0;
 height: 5px;
 display: block;
}

.progress-bar {
 background-color: var(--global-theme-color);
 width: 0%;
 display: block;
 height: inherit;
}

// Theme transition
html.transition,
html.transition *,
html.transition *::before,
html.transition *::after {
 transition: all 750ms !important;
 transition-delay: 0 !important;
}

// Loading skeleton screens
.skeleton {
 animation: skeleton-loading 1s linear infinite alternate;
 background: linear-gradient(90deg,
 var(--global-bg-color) 0%,
 rgb(128 128 128 / 8%) 50%,
 var(--global-bg-color) 100%);
 background-size: 200% 100%;
}

@keyframes skeleton-loading {
 0% {
 background-position: 200% 0;
 }

 100% {
 background-position: -200% 0;
 }
}

.skeleton-paper {
 height: 120px;
 margin: var(--space-tau) 0;
 padding: var(--space-tau);
}

.skeleton-post {
 height: 60px;
 margin-bottom: var(--space-sigma);
}

.skeleton-image {
 width: 100%;
 height: 400px;
}

.skeleton-text {
 height: 1rem;
 margin-bottom: var(--space-sigma);

 &.short {
 width: 60%;
 }

 &.medium {
 width: 80%;
 }

 &.long {
 width: 95%;
 }
}

// Hide skeleton when content is loaded
.content-loaded .skeleton {
 display: none;
}
