/*******************************************************************************
 * Self-hosted fonts
 ******************************************************************************/

// Source Serif 4 - Regular (400)
@font-face {
 font-family: 'Source Serif 4';
 src: url('/assets/fonts/SourceSerif4-Regular-subset.woff2') format('woff2'),
 url('/assets/fonts/SourceSerif4-Regular.ttf') format('truetype');
 font-weight: 400;
 font-style: normal;
 font-display: swap;
 unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD;
}

// Source Serif 4 - Semibold (600)
@font-face {
 font-family: 'Source Serif 4';
 src: url('/assets/fonts/SourceSerif4-Semibold-subset.woff2') format('woff2'),
 url('/assets/fonts/SourceSerif4-Semibold.ttf') format('truetype');
 font-weight: 600;
 font-style: normal;
 font-display: swap;
 unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD;
}

// Source Sans 3 - Regular (400)
// Using static font instead of variable for smaller subset size
@font-face {
 font-family: 'Source Sans 3';
 src: url('/assets/fonts/SourceSans3-Variable-subset.woff2') format('woff2'),
 url('/assets/fonts/SourceSans3-Variable.ttf') format('truetype');
 font-weight: 400;
 font-style: normal;
 font-display: swap;
 unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD;
}
