/*******************************************************************************
 * Blog - Posts, announcements, pagination
 ******************************************************************************/

@use "variables" as *;

// Blog header
.header-bar {
 border-bottom: 1px solid var(--global-divider-color);
 text-align: center;
 padding: var(--space-tau);

 h1 {
 color: var(--global-theme-color);
 font-size: 1.5rem;
 }
}

// Tag list
.tag-list {
 border-bottom: 1px solid var(--global-divider-color);
 text-align: center;
 padding-top: var(--space-tau);

 ul {
 justify-content: center;
 display: flow-root;

 p, li {
 list-style: none;
 display: inline-block;
 padding: var(--space-tau) var(--space-sigma);
 color: var(--global-text-color-light);
 }
 }
}

// Post list (index page)
.post-list {
 list-style-type: none;
 padding: 0;
 margin: 0;

 li {
 display: flex;
 justify-content: space-between;
 align-items: baseline;
 margin-bottom: var(--space-sigma);
 padding: var(--space-sigma);
 }

 h3 {
 margin: 0;
 font-size: 1em;
 font-weight: normal;
 }
}

// Post title (in list)
.post-title {
 color: var(--global-theme-color);
 text-decoration: none;
 font-weight: 500;
}

.post-description-line {
 display: flex;
 align-items: baseline;
 gap: 0.5rem;
 color: var(--global-text-color-light);
 font-size: 0.9em;
}

// Post meta (date, reading time)
.post-meta {
 color: var(--global-text-color-light);
 font-size: 0.9em;
 margin: 0 0 1.5em;
 padding: 0;
 text-align: center;

 .meta-separator {
 margin: 0 0.5em;
 }
}

// Pagination
.pagination {
 .page-item {
 .page-link {
 color: var(--global-text-color);

 &:hover {
 color: $black-color;
 }
 }

 &.active .page-link {
 color: $white-color;
 background-color: var(--global-theme-color);

 &:hover {
 background-color: var(--global-theme-color);
 }
 }
 }
}

// Single post page
.post-header {
 margin-top: 5em;
 text-align: center;
 margin-bottom: 5em;
}

// Post title variants
.post-title {
 font-size: 1em;
 margin-bottom: 0.2em;
 color: var(--global-text-color);

 a {
 color: inherit;
 text-decoration: none;

 &:hover {
 color: var(--global-theme-color);
 }
 }
}

// Individual post page titles
.post-header h1.post-title {
 font-size: 2.2em;
 margin-bottom: 0.3em;
 color: var(--global-theme-color);
}

.post {
 .post-description {
 color: var(--global-text-color-light);
 font-size: 0.9em;
 margin-bottom: 0.3em;
 }

 .post-meta {
 color: var(--global-text-color-light);
 font-size: 0.9em;
 margin-bottom: 0.3em;
 }
}

// Post content
.post-content {
 p {
 margin-bottom: 1rem;
 }

 img {
 width: 100%;
 height: auto;
 margin: 1.5rem 0;
 }

 // Use modular scale for post headings (slightly smaller than page headings)
 h1 { font-size: var(--type-size-lg); margin-top: 1.5em; margin-bottom: 0.75em; }
 h2 { font-size: var(--type-size-md); margin: 1.5em 0 0.6em; }
 h3 { font-size: 1.1rem; margin-top: 1.2em; }
 h4, h5, h6 { font-size: 1rem; }

 ul, ol {
 margin-left: 1rem;
 padding-left: 0;

 ul, ol {
 margin: 0.2rem 0 0.2rem 1rem;
 }
 }

 li {
 margin-bottom: 0.2rem;

 &:last-child {
 margin-bottom: 0;
 }
 }

 a {
 color: var(--global-theme-color);
 text-decoration: none;

 &:hover {
 color: var(--global-hover-color);
 text-decoration: underline;
 text-decoration-thickness: 0.06em;
 text-underline-offset: 0.05em;
 font-weight: 600;
 }
 }

 em, i { font-style: italic; }
 strong, b { font-weight: 600; }

 del {
 text-decoration: line-through;
 color: var(--global-text-color-light);
 }

 blockquote {
 background: var(--global-bg-color);
 border-left: 2px solid var(--global-theme-color);
 margin: 1em 0;
 padding: 0.5em 1.25rem;
 font-size: 1rem;

 p {
 color: var(--global-text-color-light);
 }

 > :last-child {
 margin-bottom: 0;
 }
 }
}

// Margin notes (footnotes shown in right margin on wide screens)
// JS implementation in _includes/scripts/littlefoot.html
.margin-note-panel {
 position: absolute;
 left: calc(100% + 2rem);
 width: 260px;
 padding: 0.75rem 1rem;
 background: var(--global-bg-color, #fff);
 border: 1px solid var(--global-divider-color, #eee);
 border-radius: 4px;
 box-shadow: none;
 font-size: 0.8rem;
 line-height: 1.5;
 color: var(--global-text-color-light, #666);
 z-index: 100;
 opacity: 0;
 visibility: hidden;
 transition: opacity 0.2s ease, visibility 0.2s ease;

 &.visible {
 opacity: 1;
 visibility: visible;
 }

 p {
 margin: 0;

 & + p {
 margin-top: 0.5rem;
 }
 }

 a {
 color: var(--global-theme-color);
 }

 @media (width <= 1400px) {
 display: none;
 }
}

.footnote-ref {
 vertical-align: baseline;
 margin: 0 0.1em;

 a {
 text-decoration: none;
 background: transparent;
 color: var(--global-text-color-light, #666);
 border: none;
 padding: 0;
 font-size: 0.75em;
 vertical-align: 0.3em;
 transition: color 0.2s ease;
 }

 a:hover,
 &.active a {
 color: var(--global-theme-color, #8c1515);
 background: transparent;
 }
}

// Post footer (related posts)
.post-footer {
 margin-top: 2em;
 color: var(--global-text-color-lighter);
 padding-top: 1.5em;

 h3 {
 font-size: 1.6em;
 font-weight: 400;
 margin-bottom: 1em;
 }

 ul {
 list-style: none;
 padding: 0;
 display: grid;
 grid-template-columns: repeat(2, 1fr);
 gap: 1em;
 }

 li {
 padding: 1em;
 margin: 0;
 border-top: 1px solid var(--global-divider-color);

 &:hover a {
 color: var(--global-theme-color);
 }
 }

 a {
 color: inherit;
 text-decoration: none;
 display: block;
 font-weight: 500;
 margin-bottom: var(--space-sigma);
 }

 p {
 font-size: 0.9em;
 color: var(--global-text-color-light);
 margin: 0;
 }

 span {
 color: var(--global-text-color-lighter);
 }
}
