/******************************************************************************
 * Table of Contents (ToC)
 *
 * Layout measurements are defined in _variables.scss.
 * JS offset values are documented in toc.js (search: "sync with _variables.scss")
 ******************************************************************************/

@use "sass:color";
@use "variables" as *;

.post-layout {
 position: relative;
}

.post-main {
 max-width: $post-content-width;
 margin: 0 auto;
}

.post-sidebar {
 position: fixed;
 left: calc(50% - #{$toc-sidebar-offset});
 top: $toc-sidebar-top;
 width: $toc-sidebar-width;
 max-height: calc(100vh - 8rem);
 overflow: hidden auto;
 z-index: 10;

 @media (width <= $toc-breakpoint) {
 display: none;
 }
}

#toc {
 padding: 0;
 position: relative;

 h4 {
 margin: 0 0 0.5rem;
 font-size: 0.85rem;
 font-weight: 500;
 color: var(--global-text-color-light);
 }

 ul {
 list-style: none;
 padding: 0;
 margin: 0;
 }

 li {
 margin: 0;

 &.toc-h2 {
 padding-left: 0.75em;
 }
 }

 a {
 text-decoration: none;
 color: var(--global-text-color-light);
 display: block;
 padding: 0.2rem 0;
 font-size: 0.8rem;
 line-height: 1.3;
 transition: color 0.2s ease;

 &:hover {
 color: var(--global-text-color);
 }

 &.active {
 color: var(--global-text-color);
 font-weight: 500;
 }
 }
}

html {
 scroll-behavior: smooth;
}

.post-content h1, .post-content h2, .post-content h3,
.post-content h4, .post-content h5, .post-content h6 {
 transition: all 0.3s ease;
 border-radius: 4px;
 padding: 0.2rem 0.5rem;
 margin-left: -0.5rem;
 margin-right: -0.5rem;

 &.toc-highlight {
 animation: toc-flash 1s ease-out forwards;
 }
}

@keyframes toc-flash {
 0% {
 background-color: color.scale(#8C1515, $lightness: 51.1461318052%);
 box-shadow: 0 0 10px rgb(140 21 21 / 30%);
 transform: translateX(8px);
 }

 25% {
 background-color: color.scale(#8C1515, $lightness: 58.4416883117%);
 box-shadow: 0 0 8px rgb(140 21 21 / 20%);
 }

 50% {
 background-color: color.scale(#8C1515, $lightness: 65.7372448183%);
 box-shadow: 0 0 5px rgb(140 21 21 / 10%);
 }

 100% {
 background-color: transparent;
 box-shadow: none;
 transform: translateX(0);
 }
}
