/*******************************************************************************
 * Navigation - Navbar, footer, dropdowns
 ******************************************************************************/

@use "variables" as *;

// Footer base
#footer {
 height: auto;
 width: 100%;
 margin: var(--space-2tau) 0 0;
 padding: var(--space-2tau) 0;
 clear: both;
 position: relative;
}

// Navbar
.navbar {
 box-shadow: none;
 border-bottom: 1px solid var(--global-divider-color);
 background-color: var(--global-bg-color);
 opacity: 0.95;

 .dropdown-menu {
 background-color: var(--global-bg-color);
 border: 1px solid var(--global-divider-color);

 a:not(.active) {
 color: var(--global-text-color);
 }

 a:hover {
 color: var(--global-hover-color);
 }

 .dropdown-divider {
 border-top: 1px solid var(--global-divider-color) !important;
 }
 }

 &.navbar-light {
 a:hover {
 text-decoration: none;
 }

 .navbar-brand {
 font-family: var(--font-serif);
 font-size: 1.8rem;
 padding: 0.2rem 0;
 font-weight: 600 !important;

 &.social {
 padding: 0;
 font-size: 1.7rem;

 a i::before {
 transition: all 0.2s ease-in-out;
 }

 a:hover i::before {
 color: var(--global-theme-color);
 }
 }
 }

 .navbar-nav .nav-item {
 .nav-link:hover {
 color: var(--global-hover-color);
 }

 &.active > .nav-link {
 font-weight: 600;
 color: var(--global-theme-color);

 &:hover {
 color: var(--global-hover-color);
 }
 }
 }
 }
}

// Dropdown items
.dropdown-item:hover {
 color: var(--global-hover-color);
 background-color: var(--global-bg-color);
}

// Mobile menu toggle
.navbar-toggler {
 .icon-bar {
 display: block;
 width: 22px;
 height: 2px;
 background-color: var(--global-text-color);
 border-radius: 1px;
 margin-bottom: 4px;
 transition: all 0.2s;
 }

 .top-bar {
 transform: rotate(45deg);
 transform-origin: 10% 10%;
 }

 .middle-bar {
 opacity: 0;
 }

 .bottom-bar {
 transform: rotate(-45deg);
 transform-origin: 10% 90%;
 }
}

.navbar-toggler.collapsed {
 .top-bar {
 transform: rotate(0);
 }

 .middle-bar {
 opacity: 1;
 }

 .bottom-bar {
 transform: rotate(0);
 }
}

// Social section (bottom of page)
.social {
 padding: var(--space-2tau) 0;
 text-align: center;
 background-color: #222;

 .contact-icon {
 font-size: 1rem;

 .contact-separator, h4 {
 color: #c7c7c7; // WCAG AAA compliant (7.1:1 contrast on #222 background)
 }

 a {
 color: white;

 &:hover {
 color: var(--global-theme-color);
 }
 }
 }

 .contact-note {
 margin-top: var(--space-sigma);
 font-size: 1rem;
 color: var(--global-text-color-lighter);
 }
}

// Footer (sticky)
footer.fixed-bottom {
 background-color: var(--global-footer-bg-color);
 font-size: 0.75rem;

 .container {
 color: var(--global-footer-text-color);
 padding: var(--space-sigma) 0;
 }

 a {
 color: var(--global-footer-link-color);

 &:hover {
 color: var(--global-theme-color);
 text-decoration: none;
 }
 }
}

footer.sticky-bottom {
 padding-bottom: 0;
}
